.bienvelope-fill-icon,
.bilinkedin-icon,
.phinstagram-logo-fill-icon {
  height: 48px;
  width: 48px;
  position: relative;
  overflow: hidden;
  flex-shrink: 0;
  min-height: 48px;
}
.icons {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-5xl);
}
.madelyn-torff-2021 {
  position: relative;
}
.copyright,
.footer-icons {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  padding: 0 var(--padding-6xl) 0 var(--padding-5xl);
}
.footer-icons {
  align-self: stretch;
  flex-direction: column;
  align-items: center;
  padding: 0 var(--padding-xl);
  gap: var(--gap-13xl);
  text-align: left;
  font-size: var(--body-1-size);
  color: var(--font-medium-emphasis);
  font-family: var(--font-nunito);
}